import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

let store = new Vuex.Store({
    state:{
        data:'',
        uuid:'',
        uuid_2:''
    },
    mutations:{
        data(state,arg){
            state.data = JSON.stringify(arg)
            window.localStorage.setItem('data_2', state.data) //只不过是一个存储的地方
            if(new Date().getTime() - JSON.parse(state.data).timeStamp >JSON.parse(state.data).time){
                window.localStorage.clear()
                state.data = '{"msg":"clear"}'
            }
        },
        uuid(state,arg){
            state.uuid = arg
        },
        uuid_2(state,arg){
            state.uuid_2 = arg
        }
    },
    actions:{
        getData({commit},arg){
            commit('data',arg)
        },
        getUuid({commit},arg){
            commit('uuid',arg)
        },
        getUuid_2({commit},arg){
            commit('uuid_2',arg)
        }
    }
})

export default store


