<template>
    <div class="layout">
        <Header></Header>
        <div class="showArticle_wrapper">
            <p class="showArticle_user">{{msg.user}}</p>
            <div>标题{{msg.title}}</div>
            <div v-for="(item,index) in data_arr" :key="index">{{item.name}}</div>
            <p class="showArticle_time">编辑于 {{day}}</p>
            <div class="showArticle_navigation">
                <div class="attitude">赞同</div>
                <div class="attitude disagree">不赞同</div>
                <div class="comment_number" @click="showComment" v-show="showNum%2 == 0">{{n}}条评论</div>
                <div class="comment_number" @click="showComment" v-show="showNum%2 != 0">收起评论</div>
            </div>
            <div v-show="showNum%2 != 0" class="showArticle_container">
                <div class="showArticle_topBar">{{n}}条评论</div>
                <div v-for="item_1 in comment_1" :key="item_1.id" class="comment_list">
                    {{item_1.user}}
                    <div class="comment_content">
                        <p>{{item_1.content}}</p>
                        <div class="resBar">
                            <span @click="showRes(item_1)">回复</span>
                        </div>
                        <div v-show="item_1.isShow_res % 2">
                            <input type="text" v-model="response[item_1.id]" class="comment_input" :placeholder="'回复'+item_1.user">
                            <button @click="sendRes_1(item_1,getComment_2)" class="comment_send" :disabled="response[item_1.id] != ''?false:true">发送</button>
                        </div>
                        <span @click="getComment_2(item_1)" class="class" v-show="item_1.isParent == 1">查看二级评论</span>
                        <span @click="hidComment_2(item_1,comment_2)" class="class" v-show="item_1.isParent == 1">关闭二级评论</span>
                    </div>
                    <div class="comment_content_2">
                        <div v-for="item_2 in comment_2" :key="item_2.id" v-show="item_2.belongId == item_1.commentId" class="comment_layout_2">
                            {{item_2.user}}回复{{item_2.belongUser}}:
                            <p>{{item_2.content}}</p>
                            <div class="resBar">
                                <span @click="showRes(item_2)">回复</span>
                            </div>
                            <div v-show="item_2.isShow_res % 2">
                                <input type="text" v-model="response_2[item_2.id]" class="comment_input" :placeholder="'回复'+item_2.user">
                                <button @click="sendRes_2(item_2,getComment_3)" class="comment_send" :disabled="response_2[item_2.id] != ''?false:true">发送</button>
                            </div>
                            <span @click="getComment_3(item_2)" class="class" v-show="item_2.isParent == 1">查看三级评论</span>
                            <span @click="hidComment_2(item_2,comment_3)" class="class" v-show="item_2.isParent == 1">关闭三级评论</span>
                            <div v-for="item in comment_3" :key="item.id" v-show="item.belongId == item_2.commentId" class="comment_layout_3">
                                {{item.belongUser}}回复{{item.user}}:
                                <p>{{item.content}}</p>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="addComment">
                    <input type="text" v-model="comment" class="comment_input" placeholder="评论">
                    <button @click="postComment" class="comment_send">提交评论</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Header from '../components/header'
export default {
    name:'showArticle',
    components:{
        Header
    },
    data(){
        return{
            msg:{},
            n:0,
            showNum:0,
            isShowRes:0,
            data_arr:[],
            response:[],
            response_2:[],
            comment:'',
            comment_1:[],
            comment_2:[],
            comment_3:[],
            arr:[]
        }
    },
    computed: {
        showData() {
            let localData = JSON.parse(window.localStorage.getItem("data_2"))
            if (localData) {
                this.$store.dispatch("getData", localData)
                return JSON.parse(this.$store.state.data)
            }
            return ''
        },
        uuid_2(){
            return this.$store.state.uuid_2
        },
        dateTime(){
            return new Date(parseInt(this.msg.time)).toLocaleString().replace(/\//g,'-')
        },
        day(){
            let n = this.dateTime.indexOf(' ')
            return this.dateTime.substring(0,n)
        }
    },
    mounted(){
        if (this.showData.msg == 'clear') {
            alert("需要重新登陆")
            this.$router.push("/login")
        }
        this.axios({
            method:'get',
            url:`http://localhost:3000/articleShow/${this.$route.params.id}`,
            responseType:'json'
        }).then(res => {
            this.msg = res.data.msg[0]
            this.arr = this.msg.article.split('<br>')
            this.arr.forEach(item =>{
                let obj = new Object()
                obj['name'] = item
                this.data_arr.push(obj)
            })
        }).catch(() => {
            console.log('失败啦呜呜')
        })
        this.getComment()
    },
    methods:{
        showComment(){
            this.showNum +=1
        },
        postComment(){
            this.$store.dispatch('getUuid_2',this.uuid())
            if(this.showData =='' || this.showData.code == 0){
                this.$router.push('/login')
                alert('请先登录去')
            }else{
                this.axios({
                    method:'post',
                    url:'http://localhost:3000/comment',
                    data:this.qs.stringify({user:this.showData.name,articleId:this.$route.params.id,commentId:this.uuid_2,content:this.comment}),
                    responseType:'json'
                }).then(res =>{
                    this.comment = ''
                    this.getComment()
                }).catch(() =>{
                    console.log('出错啦')
                })
            }
        },
        getComment(){
            this.axios({
                method:'post',
                url:'http://localhost:3000/comment/getData',
                data:this.qs.stringify({articleId:`${this.$route.params.id}`}),
                responseType:'json'
            }).then(res =>{
                console.log(res)
                this.n = res.data.msg.length
                res.data.msg.forEach(item =>{
                    if(item.belongId == null){
                        this.comment_1.push(item)
                    }
                })
                let obj = {}
                this.comment_1 = this.comment_1.reduce((cur,next) => {
                    if(next.isParent == 1){
                        cur.forEach((item,key) =>{
                            if(item.id == next.id){
                                item.isParent = 1
                            }
                        })
                    }
                    obj[next.id] ? "" : obj[next.id] = true && cur.push(next)
                    return cur
                },[])
            }).catch(() =>{
                console.log('失败啦')
            })
        },
        getComment_2(item){
            this.arr.push(item)
            this.axios({
                method:'post',
                url:'http://localhost:3000/comment/getData_2',
                data:this.qs.stringify({commentId:item.commentId}),
                responseType:'json'
            }).then(res =>{
                res.data.msg.forEach(item =>{ 
                    this.comment_2.push(item)
                })
                //去重
                // this.comment_2 = this.method.removeDuplicate(this.comment_2,'id')
                let obj = {}
                this.comment_2 = this.comment_2.reduce((cur,next) => {
                    if(next.isParent == 1){
                        cur.forEach((item,key) =>{
                            if(item.id == next.id){
                                item.isParent = 1
                            }
                        })
                    }
                    obj[next.id] ? "" : obj[next.id] = true && cur.push(next)
                    return cur
                },[]) 
            }).catch(() =>{
                console.log('失败啦')
            })
        },
        hidComment_2(item,comment){
            let arr = []
            comment.forEach((msg,index) =>{ 
                if(msg.belongId == item.commentId){
                    arr.push(index)
                }
            })
            let n = arr.length
            comment.splice(arr[0],n)
        },
        getComment_3(item){
            this.axios({
                method:'post',
                url:'http://localhost:3000/comment/getData_2',
                data:this.qs.stringify({commentId:item.commentId}),
                responseType:'json'
            }).then(res =>{
                console.log(res.data.msg)
                res.data.msg.forEach(item =>{
                    this.comment_3.push(item)
                })
                this.comment_3 = this.method.removeDuplicate(this.comment_3,'id')
            }).catch(() =>{
                console.log('失败啦')
            })
        },
        sendRes_1(item,callback){
            this.$store.dispatch('getUuid_2',this.uuid())
            this.axios({
                method:'post',
                url:'http://localhost:3000/comment/response',
                data:this.qs.stringify({user:this.showData.name,articleId:this.$route.params.id,commentId:this.uuid_2,belongId:item.commentId,belongUser:item.user,content:this.response[item.id]||this.response_2[item.id]}),
                responseType:'json'
            }).then(res =>{
                this.response[item.id] = ""
                this.response_2[item.id] =""
                callback(item)
                this.getComment()
            }).catch(() =>{
                console.log('出错啦')
            })
        },
        sendRes_2(item,callback){
            this.$store.dispatch('getUuid_2',this.uuid())
            this.axios({
                method:'post',
                url:'http://localhost:3000/comment/response',
                data:this.qs.stringify({user:this.showData.name,articleId:this.$route.params.id,commentId:this.uuid_2,belongId:item.commentId,belongUser:item.user,content:this.response[item.id]||this.response_2[item.id]}),
                responseType:'json'
            }).then(res =>{
                this.response[item.id] = ""
                this.response_2[item.id] =""
                callback(item)
                this.arr.forEach(item =>{
                    this.getComment_2(item)
                })
            }).catch(() =>{
                console.log('出错啦')
            })
        },
        showRes(item){
            item.isShow_res +=1
        }
    }
}
</script>

<style lang="scss">
@import '../scss/showArticle.scss';
</style>



