<template>
    <div>
      <router-link to="/person">返回到个人页</router-link>
      <p>个人编辑写文章</p>
      <p>{{process}}</p>
      title:<input type="text" v-model="inputTitle" value="title" >
      <Editor></Editor>
    </div>
</template>

<script>
export default {
    name:'editPage',
    components:{
        Editor: () => import('../components/editor.vue')
    },
    data(){
        return {
            inputInfo:'',
            inputTitle:'',
            process:'',
            arr:[],
            arr_2:[],
            str:''
        }
    },
    computed:{
        count(){
            if(this.$route.params.id != undefined){
                console.log(this.$route.params.id)
                this.$router.push(`/draf/${this.$route.params.id}`)
            }else if(this.$store.state.uuid){
                 this.$router.push(`/draf/${this.$store.state.uuid}`)
            }
            return this.$store.state.uuid
        },
        showData(){
            let localData = JSON.parse(window.localStorage.getItem('data_2'))
            if(localData){
                this.$store.dispatch('getData',localData)
                return JSON.parse(this.$store.state.data)
            }
            return ''
        }
    },
    mounted(){
        console.log(this.count)  //这里是必须的
        this.createDraf()
        this.showDraf()
        if(this.showData.name == undefined){
            alert('需要重新登陆哦')
            this.$router.push('/login')
        }
    },
    watch:{
        inputInfo(newVal,oldVal){
            this.process = '草稿保存中...'
            setTimeout(() =>{
                if(newVal == this.inputInfo){
                    this.createDraf()
                }
            },2000)
        },
        inputTitle(newVal,oldVal){
            this.process = '草稿保存中...'
             setTimeout(() =>{
                if(newVal == this.inputTitle){
                    this.createDraf()
                }
            },2000)
        }
    },
    methods:{
        enter(){
            this.arr = this.inputInfo.split('\n')
            this.str = this.arr.join('<br>')
        },
        submit(){
            this.axios({
                method:'post',
                url:'http://localhost:3000/edit',
                data:this.qs.stringify({drafid:this.$route.params.id,article:this.inputInfo,title:this.inputTitle,user:this.showData.name,time:Date.now(),type:1}),
                responseType:'json'
            }).then(res => {
                this.inputInfo = ''
                this.inputTitle = ''
                this.$router.push('/person/articleList')
            }).catch(() => {
                console.log('失败啦')
            })
        },
        createDraf(){
            if(this.inputInfo == '' & this.inputTitle == ''){
                return 
            }
            this.axios({
                method:'post',
                url:'http://localhost:3000/edit/insertDraf',
                 data:this.qs.stringify({drafid:this.$route.params.id,article:this.inputInfo,title:this.inputTitle,user:this.showData.name,time:Date.now(),type:0}),
                responseType:'json'
            }).then(res => {
                this.process = '已经保存到草稿箱里了'
                console.log(res)
            }).catch(() => {
                console.log('失败了')
            })
        },
        showDraf(){
            this.axios({
                method:'get',
                url:`http://localhost:3000/edit/${this.$route.params.id}`,
                data:this.qs.stringify({drafid:this.$route.params.id}),
                responseType:'json'
            }).then(res => {
                this.inputInfo = res.data.msg[0].article
                this.inputTitle = res.data.msg[0].title
            }).catch(() => {
                console.log('失败了')
            })
        }
    }
}
</script>

<style>
    textarea{
        display: block
    }
</style>





