<template>
    <div class="wrapper_logout">
        <div @click="logout">退出登陆</div>
    </div>
</template>

<script>
export default {
    name:'logout',
    methods:{
        logout(){
            this.axios({
                method:'post',
                url:'http://localhost:3000/login/logout',
                responseType: 'json',
                withCredentials:true //支持cookie跨域
            }).then(res => {
                this.$store.dispatch('getData',res.data)
            }).catch(() => {
                console.log('失败啦')
            })
        }
    }
}
</script>

<style>
    .wrapper_logout{
        margin-top:10px;
    }
</style>



