<template>
    <div class="wrapper">
        <div class="regin">
            <router-link to="/regin">注册</router-link>
        </div>
        <div class="login">
            <router-link to="/login" v-show="!showData.code">登陆</router-link>
            <div v-show="showData.code" @click="showBox">
                欢迎{{showData.name}}
            </div>
        </div>
        <ul class="handle" v-show="showData.code&&isShowBox">
            <router-link to="/person/articleList">个人中心</router-link>
            <router-link to="/">创作中心</router-link>
            <logout></logout>
        </ul>
    </div>
</template>

<script>
import logout from '../common/logout'
export default {
    name:'Header',
    components:{
        logout
    },
    data(){
        return{
            isShowBox:false
        }
    },
    computed:{
        showData(){
            let localData = JSON.parse(window.localStorage.getItem('data_2'))
            console.log(localData)
            if(localData){
                this.$store.dispatch('getData',localData)
                return JSON.parse(this.$store.state.data)
            }
            return ''
        }
    },
    mounted() {
    if (this.showData.msg == 'clear') {
        alert("需要重新登陆")
        this.$router.push("/login")
        }
    },
    methods:{
        showBox(){
            this.isShowBox = !this.isShowBox
        }
    }
}
</script>

<style lang="scss">
    @import "../scss/header.scss";
</style>



