<template>
  <div>
    这里是我的草稿
    <div v-for="(item, index) in msg" :key = "index">
        <Article :articleInfo = "item"></Article>
    </div>
  </div>
</template>

<script>
import Article from '../common/article'
export default {
  name: "drafList",
  components:{
      Article
  },
  data(){
      return{
          msg:[],
      }
  },
  computed: {
    showData() {
      let localData = JSON.parse(window.localStorage.getItem("data_2"))
      if (localData) {
        this.$store.dispatch("getData", localData)
        return JSON.parse(this.$store.state.data)
      }
      return ''
    }
  },
  mounted() {
    this.axios({
        method:'get',
        url:`http://localhost:3000/person/drafList?name=${this.showData.name}`,
        responseType:'json'
        }).then(res => {
            this.msg = res.data.msg
        }).catch(() => {
            console.log('失败啦')
        })
  }
};
</script>

<style>
</style>



