<template>
    <div class="layout">
        <Header></Header>
        <div class="wrapper_home">
            <div class="content_home">
                <div v-for="item in arr" :key="item.id">
                    <Article :articleInfo="item"></Article>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Header from './header'
import Article from '../common/article'
export default {
    name:'homePage',
    components:{
        Header,
        Article
    },
    data(){
        return{
            arr:[]
        }
    },
    mounted(){
        this.axios({
            method:'post',
            url:'http://localhost:3000'
        }).then(res => {
            this.arr = res.data.msg
        }).catch(() => {
            console.log('出错啦')
        })
    }
}
</script>

<style lang="scss">
@import '../scss/homePage.scss';
</style>




